const ListComments = ({ comments }) => (
    <>
        <h4 className='text-start ps-2'>Comments:</h4>
        {comments.map((x, key) => (
            <div className='card m-2' key={key}>
                <h5 className='card-header text-start'>{x.username}</h5>
                <p className='card-body card-text text-start'>{x.comment}</p>
            </div>
        ))}
    </>
);
export default ListComments;