import { useState } from "react";

const CommentForm = ({ articleName, setArticleInfo }) => {
    const [username, setUsername] = useState('');
    const [comment, setComment] = useState('');

    const addComment = async () => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'POST',
            body: JSON.stringify({ 'username': username, 'comment': comment }),
            headers: { 'Content-Type': 'application/json' }
        })
        const body = await result.json();
        setArticleInfo(body);
    }
    
    return (
        <form onSubmit={() => addComment()} className='p-2'>
            <h4 className='text-start'>Add Comment:</h4>
            <div className='form-floating mb-2'>
                <input className='form-control' id='username' placeholder='Username' value={username} onChange={(e => setUsername(e.target.value))} required />
                <label htmlFor='username' className='form-label'>Name</label>
            </div>
            <div className='form-floating mb-2'>
                <textarea className='form-control' id='comment' placeholder='Leave a comment here' style={{ height: '100px' }} value={comment} onChange={(e => setComment(e.target.value))} required />
                <label htmlFor='comment' className='form-label'>Comment</label>
            </div>
            <div className='mb-5'>
                <button className='btn btn-primary float-end' type='submit'>Submit</button>
            </div>
        </form>
    )
}
export default CommentForm;