import { AiFillLike } from 'react-icons/ai';
const UpVoteButton = ({ articleName, upvotes, setArticleInfo }) => {
    const upvote = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`);
        const body = await result.json();
        setArticleInfo(body);
    }
    return (
        <>
            <button className='btn btn-outline-primary me-1' type='button' onClick={() => upvote()}><AiFillLike /></button>
            <p className='d-inline-block text-muted'>This article has been liked {upvotes} times</p>
        </>
    )
}
export default UpVoteButton;