import { useState, useEffect } from 'react';
import articles from '../components/article-content';
import UpVoteButton from '../components/UpVoteButton';
import ListComments from '../components/ListComments';
import CommentForm from '../components/CommentForm';

const Article = ({match}) => {
    const name = match.params.name;
    const article = articles.find(x => x.name === name);
    const [articleInfo, setArtitleInfo] = useState({
        upvotes: 0,
        comments: []
    });
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArtitleInfo(body);
        }
        fetchData();
    }, [name])
    return (
        <>
            <h1>{article.title}</h1>
            <UpVoteButton articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArtitleInfo} />
            {article.content.map((paragraph, key) => (
                <p className='text-start ps-5 pe-5' key={key}>{paragraph}</p>
            ))}
            <ListComments comments={articleInfo.comments} />
            <CommentForm articleName={name} setArticleInfo={setArtitleInfo} />
        </>
    );
}
export default Article;