import { Link } from 'react-router-dom';

const ListArticles = ({ articles }) => (
    <>
        {articles.map((article, key) => (
            <div className='d-grid' key={key}>
                <button className='btn btn-outline-secondary p-0 text-dark border-dark m-2'>
                    <Link className='card bg-transparent' key={key} to={`/article/${article.name}`}>
                        <h4 className='card-header'>{article.title}</h4>
                        <p className='card-text text-start m-3'>{article.content[0].substring(0, 125)}...</p>
                    </Link>
                </button>
            </div>
        ))}
    </>
);

export default ListArticles;